<template>
    <div class="sorting">
        <div class="sorting__item">
            <h1 class="sorting__item_title"> Сортировка </h1>
            <div class="sorting__item_list">
                <a class="sorting__item_list_link" @click="sortedRepositoryByName">ИМЯ</a>
                <a class="sorting__item_list_link" @click="sortedRepositoryByName">ЗВЕЗДЫ</a>
                <a class="sorting__item_list_link" @click="sortedRepository">ДАТА</a>
            </div>
        </div>
        <div class="sorting__box">
            <div class="sorting__box_left" v-for="repos in repositories" :key="repos.id">
                <div>
                    <h3>{{ repos.name }}</h3>
                    <p class="sorting__box_left_text">Кол-во звёзд: {{ repos.forks }}</p>
                    <p class="sorting__box_left_text">Дата добавления: {{ repos.created_at }}</p>
                </div>
                <div class="sorting__box_left_link">
                    <a class="main__left_link" :href="repos.html_url" target="_blank">ПОСЕТИТЬ</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';

    export default {
        computed: {
            ...mapState(['repositories']),
        },
        methods: {
            sortedRepository(){
                return this.repositories.sort((a,b) => b.created_at.localeCompare(a.created_at), )
            },
            sortedRepositoryByName(){
                return this.repositories.sort((a,b) => a.name.localeCompare(b.name)) 
            },
            sortedRepositoryBystar(){
                return this.repositories.sort((a,b) => a.forks - b.forks) 
            }
        },  
    }
</script>

