import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    user: '',
    users: null,
    repositories: []
  },
  getters: {
  },
  mutations: {
    setUser(state,user) {
      state.user = user
    },
    getUsers(state, userInfo) {
      state.users = userInfo
    },
    setRepositories(state, repositories){
      state.repositories = repositories
    }
  },
  actions: {
    async getUsers(context,user) {
      try {
        let response = await axios.get(`https://api.github.com/users/${user}`)
        // let {
        //   avatar_url,
        //   created_at,
        //   login,
        //   public_repos,
        //   followers,
        //   following,
        //   html_url
        // } 

        let userInfo = {
          ...response.data,
          name: user
        }
        // console.log(userInfo);
        context.commit('getUsers',userInfo)


        // Sorting repositories
        let reposRespons = await axios.get(`https://api.github.com/users/${user}/repos?per_page=5`)
        let repositories = reposRespons.data
        context.commit('setRepositories', repositories)

      } catch (error) {
        console.error('Ошибка произошла при получении данных ' + error);
      }
      
    }
  },
})
